function* oddCycles(graph) {
    for (let cycleLength = 3; cycleLength <= graph.size(); cycleLength += 2) {
        for (let cycle of findCycles(graph, cycleLength)) {
            yield {
                kind: 'OddCycle',
                edges: PairSet.ofTwoWay(cycleEdgePairs(cycle)),
                result: new Set(cycle)
            };
        }
    }
}
